.login-page{
    background: #f1def3;
    height: 100vh;
    display: flex;
}
.login-container{
    width: 40%;
    margin: auto;
    padding: 20px 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: white;
    border-radius: 40px;
}
.login-fields{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.login-fields input{
    height: 40px;
    border: 1px solid #bcbcbc;
    border-radius: 10px;
}
.login-both{
    margin: 10px 0px;
}
.login-continue{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
}
.login-continue button{
    padding: 5px 10px;
    border-radius: 10px;
    color: white;
    background: #fb4747;
    border: none;
    font-size: 1.2rem;
    font-weight: bold;
    cursor: pointer;
    width: 100px;
}
.login-login{
    color: #696969;
    font-size: 1.1rem;
    text-decoration: none;
}
.login-login span{
    color: #fb4747;
    cursor: pointer;
}

@media only screen and (max-width: 1000px){
    .login-container{
        width: 45%;
    }
}
@media only screen and (max-width: 700px){
    .login-container{
        padding: 10px 5px;
        border-radius: 30px;
        width: 50%;
    }
    .login-fields{
        margin: 5px 0px;
    }
    .image1 img{
        width: 150px;
    }
    
    .login-fields input{
        height: 35px;
    }
    .login-both{
        margin: 5px 0px;
    }
    .login-continue{
        gap: 5px;
    }
    .login-continue button{
        padding: 3px 5px;
        font-size: 1rem;
        width: 80px;
    }
    .login-login{
        font-size: 0.7rem;
    }    
}
@media only screen and (max-width: 600px){
    .login-container{
        width: 60%;
    }
}

@media only screen and (max-width: 500px){
    .login-container{
        width: 80%;
    }
}

@media only screen and (max-width: 400px){
    .image1 img{
        width: 100px;
    } 
    .login-both{
        width: 80%;
    }
}