.card-body{
    display: flex;
    justify-content: space-around;
}
.card-progress{
    display: flex;
    justify-content: space-around;
}

@media only screen and (max-width: 340px){
    .card-both{
        display: flex;

    }
    .card-body{
        flex-direction: column;
    }
    .card-progress{
        flex-direction: column;
    }
}