.navbar1{
    background: rgb(195, 209, 245);
    padding: 5px 10px;
    border: none;
    outline: none;
}
.navbar1 p{
    margin-bottom: 0px;
}
.navbar1-both{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.navbar1-left{
    font-size: 1.5rem;
    font-weight: 600;
}
.navbar1-right{
    display: flex;
    align-items: center;
    list-style: none;
    gap: 10px;
    margin-bottom: 0rem;
    font-size: 1rem;
}
.item-button{
    background: rgb(67, 78, 237);
    color: white;
    padding: 5px 10px;
    border: none;
    outline: none;
    border-radius: 10px;
}

@media only screen and (max-width: 500px){
    .navbar1{
        padding: 3px 5px;
    }
    .navbar1-left{
        font-size: 1rem;
    }
    .navbar1-right{
        gap: 5px;
        font-size: 0.8rem;
    }
    .item-button{
        padding: 3px 5px;
        border-radius: 5px;
    }
}
@media only screen and (max-width: 300px){
    .navbar1-right{
        flex-direction: column;
    }
}