.logo{
    display: flex;
    gap: 5px;
}
.filters-item{
    background: rgb(227, 227, 228);
    padding: 5px;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    margin: 5px;
}
.filters-item h5{
    font-size: 1.1rem;
    margin-bottom: 0.3rem;
}
.switch-icons{
    background: rgb(227, 227, 228);
    display: flex;
    padding: 5px 10px;
    margin: 5px;
}
.grid{
    display: grid;
}
.grid1{
    display: none;
}
.grid2{
    display: none;
}
.btn{
    margin: 5px;
    font-weight: 600;
}

@media only screen and (max-width: 500px){
    .filters{
        display: grid;
        grid-template-columns: 1fr 1fr;
        padding: 3px 5px;
    }
    .filters-item h5{
        font-size: 0.9rem;
    }
    .switch-icons{
        justify-content: space-around;
    }
    .btn{
        width: 96%;
        font-size: 0.8rem;
    }
}
@media only screen and (max-width: 400px){
    .grid{
        display: none;
    }
    .grid1{
        display: grid;
    }
    .grid2{
        display: none;
    }
}
@media only screen and (max-width: 320px){
    .grid{
        display: none;
    }
    .grid1{
        display: none;
    }
    .grid2{
        display: grid;
    }
}